import React from 'react'
import Persons from './components/Persons'
import Notification from './components/Notification'
import personService from './services/persons'

import './index.css'

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      persons: [],
      newName: '',
      newPhone: '',
      showAll: true,
      filterFind: '',
      msg: null
    }
    this.deletePerson = this.deletePerson.bind(this);
  }


  deletePerson = (id, name) => {
    return () => {
      //const newPersons = this.state.persons.filter(p => p.id !== id)
      //console.log('deletePerson funktiossa: ' , newPersons)
 
      personService
        .del(id, name)
        .then(newPersons => { 
           const persons = this.state.persons.filter(p => p.name !== name) 
       this.setState({
          persons: persons
        })
      })
      .catch(error => {
        alert(`Person '${name}' on jo valitettavasti poistettu palvelimelta`)
      })
    }
  } 

  /*
   Checks that name does not exist in phone book
  */
  nameExists = (newN) => {    
    let found = this.state.persons.filter(p => p.name === newN)
    if (found.length > 0) {
      return found[0].id
    } else {
      return null
    } 
  }

  componentDidMount() {
    personService
      .getAll()
      .then(response => {
        this.setState({ persons: response })
      })
  }



  
  addPerson = (event) => {
    event.preventDefault()
    const personObject = {
      name: this.state.newName,
      number: this.state.newPhone
    }
    // Added if name does not exist
    //if (!this.nameExists(this.state.newName)) {
      if (this.nameExists(this.state.newName)===null) {
      personService
        .create(personObject)
        .then(newPerson => {
          this.setState({
            persons: this.state.persons.concat(newPerson),
            newName: '',
            newPhone: '',
            msg: "Henkilö lisätty!"
          })
          setTimeout(() => {
            this.setState({msg: null})
          }, 5000)
        })
    } 
    else {
      if (window.confirm(`Nimi ${this.state.name} on jo luetttelossa, korvataanko numero?`)) {
        personService
          .update(this.state.id,personObject)
          .then(newPerson => {
            this.setState({
              persons: this.state.persons,
              
              newName: '',
              newPhone: ''

            })
          }) 
      }
    }
  }


  handleNameChange = (event) => {
    this.setState({ newName: event.target.value })
  }
  handlePhoneChange = (event) => {
    this.setState({ newPhone: event.target.value })
  }
  handleFilterChange = (event) => {
    this.setState({ filterFind: event.target.value })
  }

  filterPersons = (event) => {
    event.preventDefault()
    if (!(this.state.filterFind==='')) {
      this.setState({
        showAll: false
      })
    } 
    else {
      this.setState({
        showAll: true,
        filterFind: ''
      })
    } 
  }
  render() {

    const personsToShow =
    this.state.showAll ?
      this.state.persons :
      this.state.persons.filter(p => (p.name.startsWith(this.state.filterFind)))

    return (
      <div>
        <h2>Puhelinluettelo</h2>

          <Notification message={this.state.msg}/>
       

        <form onSubmit={this.filterPersons}>
           Rajaa näytettäviä: <input 
                value={this.state.filterFind}
                onChange={this.handleFilterChange}
              />
        </form>
         <h3>Lisää uusi yhteystieto</h3>
        <form onSubmit={this.addPerson}>
          <div>
            Nimi: <input
                value={this.state.newName}
                onChange={this.handleNameChange}
          />
          </div>
          <div>
            Puhelin: <input 
                value={this.state.newPhone}
                onChange={this.handlePhoneChange}
          />
          </div>         
          <div>
            <button type="submit">lisää</button>
          </div>
        </form>
        <h2>Numerot</h2>
        <Persons persons={personsToShow} 
            deletePerson={this.deletePerson} />
      </div>
    )
  }
}

export default App